@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&display=swap');

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
}

.bold {
    font-weight: bold;
}

.gray {
    color: #918E9B;
}

nav {
    height: 70px;
    display: flex;
    padding: 20px 36px;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.nav-logo{
    width: 82.02px;
    height: 25.352px;
}


section {
    padding: 20px;
}

.hero {
    display: flex;
    flex-direction: column;
}

.hero-photo {
    max-width: 400px;
    align-self: center;
}

.hero-header{
    padding-left: 36px;
    color: #000;
    font-size: 36px;
    font-weight: 600;
    line-height: 110%;
    margin-bottom: 16px;
}

.hero-text {
    padding-left: 36px;
    width: 320px;
    height: 56px;
    color: #222;
    font-size: 16px;
    font-weight: 300;
    line-height: 110%;
    margin-top: 0;
}

.card{
    width: 176px;
    font-size: 12px;
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    position: relative;
}

.cards-list{
    display: flex;
    flex-wrap: nowrap;
    gap: 20px;
   overflow-x: auto;
}

.card-price {
    margin-top: auto;
}

.card-image{
    width: 100%;
    border-radius: 9px;
    margin-bottom: 9px;
}

.card-title {
    overflow: hidden;
    text-overflow: ellipsis;
}

.card-stats{
    display: flex;
    align-items: center ;
}

.card-star{
    height: 14px;
}


.card-badge{
    position: absolute;
    top: 6px;
    left: 6px;
    background-color: white;
    padding: 5px 7px;
    border-radius: 2px;
    font-weight: bold;
}